import React, { useEffect, useState } from 'react';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import { initializeApp } from "firebase/app";
import {
  getAuth,
  signInWithEmailAndPassword,
} from "firebase/auth";
import { getDatabase, ref, set, child, get } from "firebase/database";
import { Diagram } from './Diagram';

const firebaseConfig = {
  apiKey: "AIzaSyCwd5DEZmjQD0_AUJLUQcU6wh9u0--i6bQ",
  authDomain: "esp-temp-sens.firebaseapp.com",
  databaseURL: "https://esp-temp-sens-default-rtdb.europe-west1.firebasedatabase.app",
  projectId: "esp-temp-sens",
  storageBucket: "esp-temp-sens.appspot.com",
  messagingSenderId: "962595985757",
  appId: "1:962595985757:web:4434e9adbf1ec39cc8b0b4"
};

const app = initializeApp(firebaseConfig);
const auth = getAuth(app);
const database = getDatabase(app);
const dbRef = ref(database);

function App() {

  const [email, setEmail] = useState<string>();
  const [password, setPassword] = useState<string>();
  const [actual, setActual] = useState<{ [key: string]: number }>({});
  const [mapping, setMapping] = useState<{ [key: string]: string }>({});

  const logInWithEmailAndPassword = () => {
    email && password && signInWithEmailAndPassword(auth, email, password).then(() => {
      setEmail("");
      setPassword("");
    });
  };

  const readData = () => {
    get(child(dbRef, 'mapping')).then((snapshot) => {
      if (snapshot.exists()) {
        setMapping(snapshot.val());
      }
      get(child(dbRef, 'actual')).then((snapshot) => {
        if (snapshot.exists()) {
          setActual(snapshot.val());
        }
      });
    });
  };

  useEffect(() => { readData() }, []);

  const sendData = () => {
    set(ref(database, 'mapping'), mapping).then(() => readData());
  };

  return (
    <>
      <Grid container spacing={2} direction="column"
        alignItems="center"
        justifyContent="center">
        {!auth.currentUser && (
          <>
            <Grid item >
              <TextField label="email" variant="outlined" onChange={(e) => setEmail(e.target.value)} />
            </Grid>
            <Grid item >
              <TextField label="password" variant="outlined" onChange={(e) => setPassword(e.target.value)} />
            </Grid>
            <Grid item xs={12}>
              <Button onClick={logInWithEmailAndPassword} variant="outlined" disabled={!email || !password}>Login</Button>
            </Grid>
          </>)}

        {auth.currentUser && (
          <>
            <Grid item xs={12}>
              <Typography variant="h4">
                Adatok
              </Typography>
            </Grid>
            <Grid item>
              <Grid container spacing={2}>
                {Object.entries(actual).map(([key, value]) => (
                  <Grid item key={key}>
                    {mapping[key] ? (<Typography display="inline" >{mapping[key]}: </Typography>) : (<Typography>{key}: </Typography>)}
                    <Typography display="inline">{value}</Typography>
                  </ Grid>
                ))}
              </Grid>
            </Grid>
            <Grid item xs={12}>
              <Button onClick={readData} variant="outlined" color='success'>Aktuális adatok lekérdezése</Button>
            </Grid>
            <Grid item>
              <Typography variant="h4">
                Elnevezések
              </Typography>
            </Grid>
            {Object.keys(actual).map((key) => (
              <Grid item key={key}>
                <TextField label={key} variant="outlined"
                  defaultValue={mapping[key] ?? ""}
                  onChange={(e) => setMapping((previous) => { previous[key] = e.target.value; return previous; })} />
              </Grid>
            ))}
            <Grid item>
              <Button onClick={sendData} variant="outlined" color='warning'>Elnevezések módosítása</Button>
            </Grid>
            <Grid item>
              <Typography variant="h4">
                Diagram
              </Typography>
            </Grid>
          </>
        )}
      </Grid>
      {auth.currentUser && (
        <Diagram dbRef={dbRef} mapping={mapping} />
      )}
    </>
  );
}

export default App;
